import * as React from "react";
import Box from "@mui/material/Box";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import TextField from "@mui/material/TextField";
import Dialog from "@mui/material/Dialog";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import DialogContentText from "@mui/material/DialogContentText";
import DialogActions from "@mui/material/DialogActions";
import { useState, useRef, useEffect } from "react";
import ImageCropper from "./ImageCropper"; // ImageCropper

export default function Message({ langValue }) {
  const [nameValue, setNameValue] = useState("");
  const [teamValue, setTeamValue] = useState("");
  const [othersValue, setOthersValue] = useState("");
  const [fileData, setFileData] = useState(null); // 
  const [isSubmitDialogOpen, setIsSubmitDialogOpen] = useState(false);
  const [errorNameMessage, setErrorNameMessage] = useState("");
  const [errorTeamMessage, setErrorTeamMessage] = useState("");
  const [errorOthersMessage, setErrorOthersMessage] = useState("");

  console.log(nameValue + " " + teamValue + " " + othersValue);

  // const onFileInputChange = (event) => {
  //   const selectedFile = event.target.files[0]; // 

  //   if (selectedFile) {
  //     const reader = new FileReader();

  //     // 
  //     reader.onload = (e) => {
  //       const fileBinaryData = e.target.result; // 

  //       // base64
  //       const base64Data = btoa(fileBinaryData);

  //       // base64
  //       setFileData(base64Data);
  //     };

  //     // 
  //     reader.readAsBinaryString(selectedFile);
  //   }
  // };

  // JSON
  console.log(
    nameValue.length + " " + teamValue.length + " " + othersValue.length
  );

  const handleSubmit = () => {
    // JSON
    const postData = {
      name: nameValue,
      team: teamValue,
      others: othersValue,
      fileData: fileData,
    };

    console.log("File Data:", fileData); // fileData

    // PythonURL
    const backendURL = "https://party-back.onrender.com/backend"; // URL

    // POST
    fetch(backendURL, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(postData),
    })
      .then((response) => response.json())
      .then((data) => {
        // 
        console.log(data);
        window.alert("");
        window.location.reload();
      })
      .catch((error) => {
        // 
        console.error("Error:", error);
        window.alert("");
      });
  };

  const handleOpenSubmitDialog = () => {
    if (
      nameValue.length != 0 &&
      teamValue.length != 0 &&
      othersValue.length != 0 &&
      fileData != null
    ) {
      if (
        nameValue.length <= 50 &&
        teamValue.length <= 50 &&
        othersValue.length <= 300
      ) {
        setIsSubmitDialogOpen(true);
      } else {
        window.alert("");
      }
    } else {
      window.alert("");
      if (nameValue.length == 0) {
        setErrorNameMessage("");
      }
      if (teamValue.length == 0) {
        setErrorTeamMessage("");
      }
      if (othersValue.length == 0) {
        setErrorOthersMessage("");
      }
    }
  };

  const handleConfirmSubmit = () => {
    // 
    // 

    handleSubmit();

    // 
    setIsSubmitDialogOpen(false);
  };

  const handleCloseSubmitDialog = () => {
    setIsSubmitDialogOpen(false);
  };

  const handleNameChange = (event) => {
    const inputValue = event.target.value;
    setNameValue(inputValue);

    if (inputValue.length <= 30) {
      setErrorNameMessage("");
    } else {
      setErrorNameMessage("30");
    }
  };

  const handleTeamChange = (event) => {
    const inputValue = event.target.value;
    setTeamValue(inputValue);
    if (inputValue.length <= 30) {
      setErrorTeamMessage("");
    } else {
      setErrorTeamMessage("30");
    }
  };

  const handleOthersChange = (event) => {
    const inputValue = event.target.value;
    setOthersValue(inputValue);

    if (inputValue.length <= 300) {
      setErrorOthersMessage("");
    } else {
      setErrorOthersMessage("300");
    }
  };

  return (
    <>
      <Card sx={{ minWidth: 275, maxWidth: 300 }}>
        <CardContent>
          <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
            {langValue.name_nickname} <br />
            <TextField
              label="Name"
              value={nameValue} //
              onChange={handleNameChange} //
              error={errorNameMessage !== ""}
              helperText={errorNameMessage}
            />{" "}
            {/*  */}
          </Typography>
          <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
            {langValue.team}
            <br />
            <TextField
              label="Team"
              value={teamValue}
              onChange={handleTeamChange} //
              error={errorTeamMessage !== ""}
              helperText={errorTeamMessage}
            />{" "}
            {/*  */}
          </Typography>
          <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
            {langValue.others}
            <br />
            {/*  */}
            <TextField
              label="Others"
              value={othersValue}
              onChange={handleOthersChange}
              error={errorOthersMessage !== ""}
              helperText={errorOthersMessage}
            />{" "}
          </Typography>
          <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
            {langValue.your_image}
            {/*  */}
            <div>
              <ImageCropper fileData={fileData} setFileData={setFileData} />
            </div>
          </Typography>
        </CardContent>
        <CardActions>
          <Button size="small" onClick={handleOpenSubmitDialog}>
            {langValue.submit}
          </Button>
        </CardActions>
      </Card>

      <Dialog open={isSubmitDialogOpen} onClose={handleCloseSubmitDialog}>
        <DialogTitle></DialogTitle>
        <DialogContent>
          <DialogContentText></DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseSubmitDialog} color="primary">
            
          </Button>
          <Button onClick={handleConfirmSubmit} color="primary">
            
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}
