// ImageCropper.js
import React, { useState, useRef, useEffect } from "react";
import Cropper from "react-cropper";
import "cropperjs/dist/cropper.css";

function ImageCropper({ fileData, setFileData }) {
  const [croppedDataUrl, setCroppedDataUrl] = useState(null);
  const [crop, setCrop] = useState({ x: 50, y: 50 });

  const cropperRef = useRef(null);

  const onFileInputChange = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();

    reader.onload = (e) => {
      setFileData(e.target.result);
    };

    if (file) {
      reader.readAsDataURL(file);
    }
  };

  const handleCrop = () => {
    console.log(cropperRef.current);

    const x = 50; // x
    const y = 50; // y
    const fillColor = "#ffffff"; // 

    if (cropperRef.current) {
      const croppedCanvas = cropperRef.current.cropper.getCroppedCanvas({
        x,
        y,
        fillColor,
      });
      if (croppedCanvas === null) {
        // null
        console.error("");
        return;
      } else {
        setCroppedDataUrl(croppedCanvas.toDataURL());
      }
    }
  };

  useEffect(() => {
    // useState
    setFileData(croppedDataUrl);
    console.log(croppedDataUrl);
    console.log(fileData);
  }, [croppedDataUrl]);

   // 
   const resetCrop = () => {
    if (cropperRef.current) {
      cropperRef.current.cropper.reset();
    }
  };

  return (
    <div>
      <input type="file" accept="image/*" onChange={onFileInputChange} />
      {fileData && croppedDataUrl === null && (
        <div>
          <Cropper
            ref={cropperRef}
            src={fileData}
            style={{ height: "100%", width: "100%" }}
            movable={false}
            aspectRatio={5 / 3}
            cropBoxResizable={false}
            crop={crop}
          />
          <button onClick={handleCrop}></button>
          <button onClick={resetCrop}></button>

        </div>
      )}
      {croppedDataUrl && (
        <img
          src={croppedDataUrl}
          alt=""
          style={{ width: "auto", height: "100%", maxWidth: "100%" }}
        />
      )}
    </div>
  );
}

export default ImageCropper;
