import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import DialogContentText from "@mui/material/DialogContentText";
import DialogActions from "@mui/material/DialogActions";
import { useEffect, useState } from "react";
import ReactLoading from "react-loading";

function MediaCard({ langValue }) {
  const [jsonData, setJsonData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [isDeleteDialogOpen, setIsDeleteDialogOpen] = useState(false);
  const [itemToDelete, setItemToDelete] = useState(null);
  const [showAllOthers, setShowAllOthers] = useState(false);
  const [showFullText, setShowFullText] = useState([]);

  // JSON
  const fetchData = async () => {
    try {
      const response = await fetch(
        "https://party-back.onrender.com/get_json_data"
      );
      const data = await response.json();
      setJsonData(data); // JSON
      setLoading(false);
      console.log(data);
      // 20false
      const myArray = new Array(data.length).fill(false);
      setShowFullText(myArray);
    } catch (error) {
      console.error("", error);
    }
  };

  useEffect(() => {
    fetchData(); // 
  }, []); //

  const handleDelete = (itemId) => {
    setItemToDelete(itemId);
    setIsDeleteDialogOpen(true);
  };

  const deleteItem = (id) => {
    console.log(id);

    const postData = {
      id: id,
    };
    // PythonURL
    const backendURL = "https://party-back.onrender.com/delete"; // URL

    // POST
    fetch(backendURL, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(postData),
    })
      .then((response) => response.json())
      .then((data) => {
        // 
        console.log(data);
        fetchData();
        window.alert("");
        // window.location.reload();
      })
      .catch((error) => {
        // 
        console.error("Error:", error);
        // window.alert("");
      });

    // 
    setIsDeleteDialogOpen(false);
  };

  const confirmDelete = () => {
    // 
    // itemToDelete 
    deleteItem(itemToDelete);
  };

  const cancelDelete = () => {
    // 
    setIsDeleteDialogOpen(false);
  };

  // 
  const maxCharacters = 17;

  // 
  const foldText = (text) => {
    return text.length > maxCharacters ? text.slice(0, maxCharacters) : text;
  };

  console.log(jsonData);
  return (
    <>
      <div>
        {loading ? (
          <>
            <div className="popup-overlay">
              <div className="popup-content">
                <ReactLoading
                  type="spin"
                  color="#000000"
                  height="50px"
                  width="50px"
                  className="mx-auto"
                />
                <p className="text-center mt-3">Loading</p>
              </div>
            </div>
          </>
        ) : (
          <div style={{ display: "flex", flexWrap: "wrap" }}>
            {jsonData.map((item, index) => (
              <div
                key={index}
                style={{
                  flexBasis: "50%",
                  padding: "10px",
                  maxWidth: "50%",
                }}
              >
                <Card
                  variant="outlined"
                  style={{
                    width: "100%",
                    height: "auto",
                    minHeight: "400px",
                    display: "flex",
                    flexDirection: "column",
                  }}
                >
                  <div style={{ flex: "3" }}>
                    <img
                      src={item.fileData}
                      alt="Image"
                      style={{
                        width: "100%",
                        height: "100%",
                        objectFit: "cover",
                      }}
                    />
                  </div>

                  <CardContent
                    style={{
                      flex: "2",
                      wordWrap: "break-word", // word-wrap
                    }}
                  >
                    {/* <div
                      style={{
                        maxHeight: showAllOthers ? "none" : "2.4em", // 2.4em2
                        WebkitLineClamp: showAllOthers ? "unset" : 2, // 
                        display: "-webkit-box", // 
                      }}
                    > */}
                    <h5
                      style={{
                        whiteSpace: "pre-line",
                        width: "100%",
                      }}
                    >
                      {item.name}
                      {/* {item.name.match(/.{1,17}/g).join("\n")} */}
                    </h5>
                    <div
                      style={{
                        whiteSpace: "pre-line",
                        width: "100%",
                      }}
                    >
                      Team: {item.team}
                      {/* Team: {item.team.match(/.{1,17}/g).join("\n")} */}
                    </div>
                    <div
                      style={{
                        whiteSpace: "pre-line",
                        width: "100%",
                      }}
                    >
                      {showFullText[index]
                        ? item.others
                        : foldText(item.others)}
                      {item.others.length > maxCharacters && (
                        <span
                          onClick={() => {
                            const newArray = [...showFullText]; // 
                            newArray[index] = !newArray[index]; // i
                            setShowFullText(newArray); // setState
                          }}
                          style={{
                            cursor: "pointer",
                            color: "blue",
                            marginLeft: "5px",
                          }}
                        >
                          {showFullText[index] ? "" : ""}
                        </span>
                      )}

                      {/* {item.others} */}
                      {/* {item.others.match(/.{1,17}/g).join("\n")} */}
                    </div>
                    {/* {item.name.length +
                        item.team.length +
                        item.others.length >
                        50 && (
                        <button
                          onClick={() => setShowAllOthers(!showAllOthers)}
                        >
                          {showAllOthers ? "" : ""}
                        </button>
                      )}{" "} */}
                    {/* </div> */}
                  </CardContent>
                  <CardActions>
                    <Button size="small" onClick={() => handleDelete(item.id)}>
                      {" "}
                      {langValue.delete}
                    </Button>
                  </CardActions>
                </Card>

                {/*  */}
                <Dialog open={isDeleteDialogOpen} onClose={cancelDelete}>
                  <DialogTitle></DialogTitle>
                  <DialogContent>
                    <DialogContentText></DialogContentText>
                  </DialogContent>
                  <DialogActions>
                    <Button onClick={cancelDelete} color="primary">
                      
                    </Button>
                    <Button onClick={confirmDelete} color="primary">
                      
                    </Button>
                  </DialogActions>
                </Dialog>
              </div>
            ))}
          </div>
        )}
      </div>
    </>
  );
}

export default MediaCard;
